export default {
	_:null,
	connect() {
		this._ = uni.connectSocket({
			url: 'ws://localhost:9118',
			success() {
				console.log("websocket")
			}
		});
	},
	send(data){
		let _this = this;
		_this._.onOpen(function(r){
			_this._.send({
				data,
				fail(r){
					console.log(r);
				},
			})
		})
	},
	msg(cb=function(){}){
		this._.onMessage(function(res){
			cb(res);
		})
	}
}
